.techWriting-wrapper {
  margin: 0 auto;
  padding: 120px 0;
  border-bottom: 1px solid #3242aa;
  height: 100vh;
  min-height: fit-content;
}
.techWriting-content {
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 100px;
  margin: 90px auto;
}
.techWriting-main_title {
  color: #3242aa;
  font-size: 18px;
  font-family: "Permanent Marker", cursive;
  border-radius: 4px;
  text-align: center;
  margin: 0 15px;
}
.techWrittin-title_avatar{
  width: 23px !important;
  height: 23px !important;
  color: #1c1c1c !important;
  background-color: #3242aa !important;
}
.techWriting-title {
  background-color: #fff;
  font-weight: 700;
  height: 100px;
  min-width: fit-content;
  text-align: center;
  padding: 5px;
  padding-top: 30px;
  color: #ccc;
  border-top-right-radius: 7px;
  border-bottom-right-radius: 7px;
}
.techWriting-Index_img {
  display: flex;
  align-items: flex-start;
}
.techWriting-index {
  color: #ccc;
  background-color: #3242aa;
  padding: 4px;
  border-radius: 50%;
  margin: 4px;
}
.techWriting-flex {
  display: flex;
  align-items: center;
  justify-content: center;
}
.techWriting-img {
  width: 200px;
  height: 150px;
  border-radius: 3px;
  box-shadow: 1px 1px 39px 40px #3242aa;
}
.techWriting-link {
  color: #3242aa;
  margin-left: 100px;
}
@media (max-width: 320px) {
  .techWriting-img {
    width: 150px;
    height: 100px;
    box-shadow: 1px 1px 29px 30px #3242aa;
  }
  .techWriting-title {
    font-size: 12px;
    height: 85px;
  }
}
